.carouelImgHover:hover{
  border: 2px dashed black ; 
  background-color: white ;
  opacity:0.7;
}
.bullcommerceTitle{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  
}
.bullcommerceTitleInput{
  border: none;
  background-color: transparent !important;
  font-size: 49px;
  font-weight: bold;
  z-index: 2;
  text-shadow: 4px 4px 8px black;
  
}
.plusFromBama{
  position: absolute;
  color: white !important;
  /* background-color: transparent; */
  background:rgba(0,0,0,0.1);
  z-index: 3;
  right: 0;
  margin-top: 23.5rem;
  height:12px;
  width: 50px;
  text-align: center;
  justify-content: center;
  font-size: 15px;
  vertical-align:middle;
  display: flex;
  
  /* background-color: lightgray; */
  /* background-color: rgba(15, 0, 0, 0.4); */
}
.plusDesign{

  position: relative;
  bottom:20%;
  text-align: center;
  display: flex;
  justify-content:center;
}
.EH-carouselIndicators{
  z-index: 5 !important;
  margin-bottom: 0.3rem !important;
  width:95%;
}
.EH-aOfBtn{
  z-index: 2 !important;
  
}
.btnToArrow{
  /* color: "linear-gradient(94deg, #FC894D 0%, #FE5196 100%)" ; */
  height: 4rem !important;
  width:4rem !important;
}
.EH-chevron{
  font-size: 3rem !important;
  text-shadow: 5px 5px 5px black;
  /* color:#FE5196 */
  color:#292727;
}

