#store_main_navbar{
    position: fixed;
    z-index: 99;
    padding-right:5%;
    padding-left: 5%;
    width:100%;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.15) !important;
    padding-top:0.25rem;
    padding-bottom: 0.25rem;
}
.drag_icon_category{
    font-weight: 100;
    opacity: 0;
    color: #ADB0B5;
}
.drag_category .nav-item:hover>.drag_icon_category{
    opacity: 1;
}
.drag_category{
    border: 1px solid transparent;
    border-radius: 0.4rem;
    margin-right: 8px;
}
.drag_category:hover{
    border-color: #707070;
}
/* .drag_category:last-child{
    margin-right: 10px;
    background-color:red
} */
.mainNavBar{
    width: 100%;
    border: 1px solid red;
    justify-content: center;
}
@media only screen and (max-width: 600px) {
    .mainNavBar{
        width: 20rem !important;
        border: 1px solid red;
        justify-content: center !important;
    }
}
#store_main_navbar .navbar-nav{
    width: 75%;
}
#store_main_navbar .dnd-last-nav{
    width: 15%;
    border-right: 0px;
}
#store_main_navbar .nav-link{
    padding-top: 0.35rem;
}
#dnd-newCategory{
    height:30px;
    width: 30px;
    border:1px solid #707070;
    border-radius:50%;
    padding-top: 0;
}
#dnd-newCategory:hover{
    color:white !important;
    background-color: rgb(252, 137, 77);
}
.dnd-search-line{
    padding-top: 0.35rem;
}
.dnd-badge{
    left: 29px;
    top: 18px;
    /* left: 100px; */
    width: 18px;
    height: 18px;
    position: absolute;
    background-color: rgb(252, 137, 77);
    min-width: 18px !important;
    font-size: 20px;
}
