.alert__bullcommerce{
        background-color: #F6F6F6;
        border-radius:0.875rem !important;
        height: 1.5rem;
        text-align:center !important;
        float: left;
        color:#999999 !important;
        margin-bottom: 0;
        align-items: center;
        padding:0; 
}
.alert__bullcommerce .close{
        color:#333333 !important;
        text-align:center !important;
}
#notificationAlert{
        z-index:9999;
        position:absolute;
        bottom: -9%;
        left: 1.5%;
        border-radius: 3px;
        background-color: white;
        /* border-top: 5px rgba(44, 168, 255, 0.8) solid; */
        color: black;
        padding-top: 0.7rem;  
        padding-bottom: 0.7rem;
}
#notificationAlert .close{
        color: black;
        line-height: 0.3 !important;
        padding: .5rem .3rem;
        font-weight: 1;
        position: absolute;
        top: 25%;
        right: 2%;
    
}
#notificationAlert .close:hover{
       background-color: rgb(233, 233, 233);
       border-radius: 50%;
 
}